import '../assets/styles/styles.css';``
export const createText = ({
    size = '6',
    color = 'grey-08-hero',
    fontWeight = 'medium',
    label = 'Ceci est un exemple',
    cap = false
}) => {
    const txt = document.createElement('p')
    txt.className = [`fs-${size}`]
    txt.classList.add(`${color}`)
    txt.classList.add(`${fontWeight}`)
    if (cap) {
        txt.classList.add(`cap`)
    }
    txt.innerText = label

    return txt
}